@import "https://fonts.googleapis.com/css2?family=Manrope:wght@200;400;700&display=swap";
:root {
  --pure-white: #fff;
  --radial-bg: radial-gradient(50% 48.71% at 50% 50%, #fffef3 0%, #fffef3 100%);
  --radial-bg: radial-gradient(50% 48.71% at 50% 50%, #fff175 0%, #ffe500 100%);
  --grey-text: #6f6f6f;
  --white-text: #fffef3;
  --white-text-hover: #fffbd3;
  --yellow-bg: #ffe500;
  --while-open-bg: #fffef3;
}

.big {
  font-size: 28px;
}

.yellow {
  color: var(--yellow-bg);
}

.invisible {
  opacity: 0;
}

.background {
  background: var(--radial-bg);
  color: var(--white-text);
  border-radius: 6px;
  padding: 0 4px;
}

* {
